import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/shared/service/api.service';
import { LocalDataService } from 'src/app/shared/service/local-data.service';
import { environment } from 'src/environments/environment';


export interface IBusinessRole  {
  id: string;
  name: string;
  // description?: string;
}

@Component({
  selector: 'app-business-role',
  templateUrl: './business-role.component.html',
  styleUrls: ['./business-role.component.scss']
})
export class BusinessRoleComponent implements OnInit {

  public entity = 'business-role';
  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;
  public businessRoles: IBusinessRole[] = [];
  public staticbusinessRoles: IBusinessRole[] = [
    {
      id: 'manager',
      name: 'Manager'
    },
    {
      id: 'frontline',
      name: 'Frontline'
    },
    {
      id: 'supervisor',
      name: 'Supervisor'
    }
  ];

  constructor(
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private router: Router,
    private localDataService: LocalDataService
  ) { }

  ngOnInit(): void {
    this.buildForm();


    // TEMP
    if (environment.staticData) {
      this.businessRoles = this.staticbusinessRoles;
      return;
    }

    this.apiService.listMap(this.entity, {limit: 100, offset: 0}).subscribe( x => {
      this.businessRoles = x.data;
    });
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]]
    });
  }

  showAppForm(type: string, br?: IBusinessRole): void {
    if (type === 'add') {
      this.modalTitle = 'New Business Role';
    } else {
      this.modalTitle = `Edit ${br.name}`;
      this.form.setValue(br);
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name }: IBusinessRole = this.form.value;

    if (id) {
      this.update({id, name });
    } else {
      this.create({ name });
    }

    this.form.reset();
  }

  deleteApp(app: IBusinessRole): void {
    const sure = confirm('Are you sure?');
    if (sure) {
      // TEMP
      if (environment.staticData) {
        this.businessRoles = this.businessRoles.filter( o => o.id !== app.id);
        return;
      }

      this.apiService.delete(this.entity, app.id).subscribe( x => {
        this.businessRoles = this.businessRoles.filter( o => o.id !== app.id);
      });
    }
  }


  private update({id, name}): void{

    // TEMP
    if (environment.staticData) {
      this.businessRoles.map( x => {
        if (id === x.id) {
          x.name = name;
        }
      });
      return;
    }

    this.apiService.update(this.entity, id, {
      name
    }).subscribe ( x => {
      this.businessRoles.map( o => {
        if (id === o.id) {
          o.name = name;
        }
      });
    });
  }

  private create({ name }): void{

    // TEMP
    if (environment.staticData) {
      this.businessRoles.push({
        id: new Date().getTime().toString(),
        name
      });
      return;
    }

    this.apiService.create(this.entity, {
      name
    }).subscribe ( x => {
        this.businessRoles.push({
          id: x.id,
          name
        });
    });
  }

  selectApp(app: IBusinessRole): void {
    // this.localDataService.save('application', app);
    // this.router.navigate(['/businessroles/', app.id]);
  }
}
